.slider {
    position: relative;
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image {
    width: 1000px;
    height: 600px;
    border-radius: 30px;
  }
  
  .right-arrow {
    position: absolute;
    top: 45%;
    right: 35pc;
    font-size: 6rem;
    color: #1f232c;
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .left-arrow {
    position: absolute;
    top: 45%;
    left: 35pc;
    font-size: 6rem;
    color: #1f232c;
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .slide {
    opacity: 0;
    transition-duration: 1.5s ease;
  }
  
  .slide.active {
    opacity: 1;
    transition-duration: 1.4s;
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
  }
